import React from 'react'
import { injectIntl } from 'gatsby-plugin-intl'
import { Form } from 'react-bootstrap'
import { ThemeToggler } from 'gatsby-plugin-dark-mode'

const ThemeToggle = ({ intl }) => {
  return (
    <ThemeToggler>
      {({ theme, toggleTheme }) => (
        <Form.Check
          type="switch"
          id="custom-switch"
          label={intl.formatMessage({ id: "action.themeToggle" })}
          onChange={e => toggleTheme(e.target.checked ? 'dark' : 'light')}
          checked={theme === 'dark'}
        />
      )}
    </ThemeToggler>
  )
}

export default injectIntl(ThemeToggle)
